<template>
    <div>
		<h1>按下运行按钮，让数字闪动起来吧！</h1>
        
		<div class="bianliang" >
			<p id="suiji">{{num[0]}} </p> 
		</div>
		
	<!-- 出现提醒的动画 -->
		<transition-group name="animate__animated animate__bounce"
		enter-active-class="animate__heartBeat">
			<p id="tip" v-show="showtip" key="1">
			请您先点击停止键，结束当前运行
			</p>

	<!-- 为了让动画前后行距不变，加一个下面的结构,用于占位 -->
			<p v-show="!showtip" key="2"><br></p>
		</transition-group>
		
		<br>
			
	<!-- 输入框，并且双向数字绑定 -->
		<span class="kuang">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;请输入取数的个数:</span>
		<input type="number" min="1"  v-model="bian">
		<br>
		<span class="kuang">请输入时间间隔(毫秒):</span>
		<input type= "number" min="1" v-model="interval">
		<br>
	
	<!-- 出现提醒的动画 -->
		<transition-group name="animate__animated animate__bounce"
		enter-active-class="animate__heartBeat">
			<p id="warning" v-show="showwarning" key="1">
			请输入符合要求的数字
			</p>
	
	<!-- 为了让动画前后行距不变 -->
			<p v-show="!showwarning" key="2"><br></p>
		</transition-group><br>
		
	<!-- 设置按钮并绑定事件 -->
		<button @click="check(bian,interval)">运行</button>
		&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
		<button @click="stopit()">停止</button>
   </div>
</template>

<script>
//动画来自第三方的动画库
import 'animate.css'
    export default{
        name:'FiRst',
        data(){return{
            num:[Math.floor(Math.random()*10)],
			bian:10,
			interval:1000,
			statenow:true,
			showwarning:false,
			showtip:false    
        }
        },
		
		methods:{
		check(a,b){
			if(a<=0||a!=Math.floor(a)||b<=0){
				// console.log(11);
				this.showwarning=true;
				// console.log(this.showwarning)
				
				}
			//如果格式符合,运行bian()函数
			else{
					if(this.statenow==true){
						this.showwarning=false;
						this.showtip=false;
						this.start(a,b);
						this.statenow=!this.statenow
					}
					else{
						this.showtip=true;
					}
				}
		},
        start(a,b){
			let time=0;
			this.flash=window.setInterval(()=>{
				if(time<a){
					// console.log(time);
					this.$set(this.num,0,Math.floor(Math.random()*10));
					// console.log(this.num);
					time++;
				}

				else{
					this.showtip=false;
					clearInterval(this.flash);
					this.statenow=!this.statenow
			}},b)
		},
		stopit(){
			clearInterval(this.flash);
			this.statenow=!this.statenow;
			this.showtip=false
		}

    }
	}
</script>

<style scoped>


    *{margin: 3px;
	padding: 0;}

	div{margin: 0 auto;
	text-align: center;
	}

	p,span,input,button{
	display: inline-block;
	}

	.bianliang{
	background-color: lightgreen;

	margin: 0 auto;}
	
	#suiji{
	line-height: 300px;
	font-size: 300px;
	}
	
</style>